header h1{
    color: #b2995a;
    text-align: center;
}

.header-container{
    display: flex;
    flex-direction: row;
    gap: 2rem;
    justify-content: space-around;
}

@media (max-width: 1000px) {
    .header-container{
        display: flex;
        flex-direction: column;
        gap: 2rem;
        justify-content: space-around;
    }
}

.menu{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    align-items: center;
}

.menu button.active {
    color: cyan;
}

.menu button{
    padding: 15px;
    border-radius: 12px;
    border: none;
    background: none;
    font-family: "Segoe UI", Helvetica, Arial, sans-serif, "Segoe UI Emoji", "Segoe UI Symbol";
}

.menu button a {
    font-weight: 500;
    font-size: 18px;
    text-decoration: none;  /* Retire le soulignement des liens */
    color: inherit;         /* Optionnel : pour hériter de la couleur du texte parent */
}

.menu button a:hover {
    color: cyan;
    transition: ease-in 0.3s;
}

.menu button a:active {
    color: cyan;
}